        async getData(data) {
            const res = await fetch('http://localhost:20004/graphql', {
                method: 'POST',
                headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                },
                body: JSON.stringify(data)
            });
            return res.json()
        },

        async query(field, args = null) {
            return await this.getData({
                query: `query Learningunit($id: String!) {
                    learningunit(id: $id) {
                    title,
                    url,
                    mc,
                    description,
                    createdAt,
                    updatedAt,
                    deleted
                    }
                }`,
                variables: args
            })
        }
    },
    async mounted() {
        this.items = await this.query('query', {id:'061837cb-5ae1-4804-9d5d-37a7d2313c75'} );
